import { makeAutoObservable, runInAction } from 'mobx';
import { fetchRickAndMortyCharactersAPI } from '../services/api';
import { Character } from '../types';

export class CharacterStore {
  characters: Character[] = [];
  favorites: Character[] = [];
  loading = false;
  error: string | null = null;
  searchTerm = '';
  selectedStatus = 'all';
  selectedSpecies = 'all';
  showOnlyFavorites = false;

  constructor() {
    makeAutoObservable(this);
    // Solo cargar favoritos en el cliente
    if (typeof window !== 'undefined') {
      this.loadFavoritesFromStorage();
    }
  }

  setLoading(loading: boolean) {
    this.loading = loading;
  }

  setError(error: string | null) {
    this.error = error;
  }

  setSearchTerm(term: string) {
    this.searchTerm = term;
  }

  setSelectedStatus(status: string) {
    this.selectedStatus = status;
  }

  setSelectedSpecies(species: string) {
    this.selectedSpecies = species;
  }

  toggleShowOnlyFavorites() {
    this.showOnlyFavorites = !this.showOnlyFavorites;
    
    // Si se activa la vista de favoritos, limpiar otros filtros
    if (this.showOnlyFavorites) {
      this.searchTerm = '';
      this.selectedStatus = 'all';
      this.selectedSpecies = 'all';
    }
  }

  setShowOnlyFavorites(show: boolean) {
    this.showOnlyFavorites = show;
  }

  async fetchCharacters() {
    this.setLoading(true);
    this.setError(null);
    
    try {
      const fetchedCharacters = await fetchRickAndMortyCharactersAPI();
      
      runInAction(() => {
        this.characters = fetchedCharacters;
        this.loading = false;
      });
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Failed to fetch characters';
      
      runInAction(() => {
        this.error = errorMessage;
        this.loading = false;
      });
    }
  }

  toggleFavorite(character: Character) {
    const isFavorite = this.favorites.some(fav => fav.id === character.id);
    
    if (isFavorite) {
      runInAction(() => {
        this.favorites = this.favorites.filter(fav => fav.id !== character.id);
      });
    } else {
      runInAction(() => {
        this.favorites.push(character);
      });
    }
    
    this.saveFavoritesToStorage();
  }

  isFavorite(characterId: number): boolean {
    return this.favorites.some(fav => fav.id === characterId);
  }

  private loadFavoritesFromStorage() {
    try {
      const stored = localStorage.getItem('rickAndMortyFavorites');
      if (stored) {
        const favorites = JSON.parse(stored);
        runInAction(() => {
          this.favorites = favorites;
        });
      }
    } catch (error) {
      // Error silencioso al cargar favoritos
    }
  }

  private saveFavoritesToStorage() {
    try {
      localStorage.setItem('rickAndMortyFavorites', JSON.stringify(this.favorites));
    } catch (error) {
      // Error silencioso al guardar favoritos
    }
  }

  // Computed properties
  get filteredCharacters() {
    let filtered = this.characters;

    // Filter by favorites first
    if (this.showOnlyFavorites) {
      filtered = this.favorites;
    }

    // Filter by search term
    if (this.searchTerm) {
      filtered = filtered.filter(character =>
        character.name.toLowerCase().includes(this.searchTerm.toLowerCase())
      );
    }

    // Filter by status
    if (this.selectedStatus !== 'all') {
      filtered = filtered.filter(character => character.status === this.selectedStatus);
    }

    // Filter by species
    if (this.selectedSpecies !== 'all') {
      filtered = filtered.filter(character => character.species === this.selectedSpecies);
    }

    return filtered;
  }

  get uniqueStatuses() {
    return ['all', ...Array.from(new Set(this.characters.map(c => c.status)))];
  }

  get uniqueSpecies() {
    return ['all', ...Array.from(new Set(this.characters.map(c => c.species)))];
  }

  get favoritesCount() {
    return this.favorites.length;
  }

  get charactersCount() {
    if (this.showOnlyFavorites) {
      return this.filteredCharacters.length;
    }
    return this.characters.length;
  }
} 