// app/services/api.ts
import axios from 'axios';
import { Character, CharacterApiResponse } from '../types';

interface LoginResponse {
	success: boolean;
	message?: string;
	token?: string;
}

export const simulateLogin = async (username: string, password: string): Promise<LoginResponse> => {
	// Simulate login with hardcoded credentials
	const users = [
		{ username: 'user', password: 'password' },
		{ username: 'admin', password: 'admin123' },
	];

	const user = users.find(u => u.username === username && u.password === password);

	if (user) {
		const response = { success: true, message: 'Login successful', token: 'fake-jwt-token-123' };
		return response;
	} else {
		const response = { success: false, message: 'Invalid username or password' };
		return response;
	}
};

export const fetchRickAndMortyCharactersAPI = async (): Promise<Character[]> => {
	// Fetch directly from the Rick and Morty API
	const response = await axios.get<CharacterApiResponse>('https://rickandmortyapi.com/api/character');
	return response.data.results;
};
